<template>
	<div class="menu__list" ref="list">
		<div class="menu__list_item"
			v-for="category in categories"
			:key="category.id">
			<div class="list-item__wrapper"
				v-if="(category.id > 0 && category.id < categories.length - 1) && (categoriesActiveList[category.id] || categoriesActiveList[0])">
				<h2 class="list-item__title">{{ category.name }}</h2>
				<ul class="menu__sublist">
					<li class="menu__sublist_item"
						v-for="video in videoCategories[category.id - 1]"
						:key="video.id"
						@click="$parent.initVideo(video)"
						@mouseenter="$parent.startHover(video.map_id, category.id, video.id)
							setPreview(video.preview_small, video.name, $event)"
						@mouseleave="$parent.endHover()
							removePreview($event)"
					>
						<!-- @mouserover and @mouseout  -->
						<h3 class="sublist-item__title content__title">{{ video.name }}</h3>
						<p class="sublist-item__category">{{ categories[video.section_id].name }}</p>
						<div class="sublist-item__marker content__marker"
							:style="{background: categoriesColors[video.section_id]}">{{video.section_id}}</div>
						<button class="sublist-item__play content__play" type="button" @click="$parent.initVideo(video)">
							<Icon class="icon" name="play" />
						</button>
						<div class="sublist-item__preview content__preview"
							:class="{'content__preview_mobile': isMobile}"
							v-if="isMobile">
<!--							<img :src="`${$parent.publicPath}` + video.preview_small" :alt="video.name" />-->
							<img :src="video.preview_mobile" :alt="video.name" />
						</div>
					</li>
				</ul>
			</div>
			<div class="list-item__wrapper"
				v-if="(category.id === categories.length - 1) && (categoriesActiveList[category.id] || categoriesActiveList[0])">
				<h2 class="list-item__title">{{ category.name }}</h2>
				<ul class="menu__sublist">
					<li class="menu__sublist_item"
						v-for="panorama in panoramas"
						:key="panorama.id"
						@click="$parent.initVideo(panorama)"
						@mouseenter="$parent.startHover(panorama.map_id, category.id, panorama.id)
							setPreview(panorama.preview_small, panorama.name, $event)"
						@mouseleave="$parent.endHover()
							removePreview($event)"
					>
						<h3 class="sublist-item__title content__title">{{ panorama.name }}</h3>
						<p class="sublist-item__category">{{ categories[categories.length - 1].name }}</p>
						<div class="sublist-item__marker content__marker" :style="{background: categoriesColors[categories.length - 1]}">{{categories.length - 1}}</div>
						<button class="sublist-item__play content__play" type="button" @click="$parent.initVideo(panorama)">
							<Icon class="icon" name="eye" />
						</button>
						<div class="sublist-item__preview content__preview"
							:class="{'content__preview_mobile': isMobile}"
							v-if="isMobile">
<!--							<img :src="`${$parent.publicPath}` + panorama.preview_small" :alt="panorama.name" />-->
							<img :src="panorama.preview_mobile" :alt="panorama.name" />
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */
import { mapGetters } from 'vuex'
import PerfectScrollbar from '../../node_modules/perfect-scrollbar'

export default {
	name: 'MenuList',
	data() {
		return {
			// publicPath: process.env.BASE_URL,
			scroll: null
		}
	},
	props: ['categories', 'categoriesColors', 'categoriesActiveList', 'activeCategory', 'isMobile'],
	computed: mapGetters(['videoCategories', 'panoramas']),
	mounted() {
		this.setScrollbar()
	},
	watch: {
		activeCategory() {
			let container = document.querySelector('.menu__list')
			this.scroll.destroy()
			this.scroll = null
			container.style.overflow = 'hidden'
			setTimeout(() => {
				this.setScrollbar()
				container.style.overflow = ''
				container.firstElementChild.scrollIntoView()
			}, 10)
		}
	},
	methods: {
		setScrollbar() {
			const container = document.querySelector('.menu__list')
			this.scroll = new PerfectScrollbar(container, {
				suppressScrollX: true
			})
			container.addEventListener('ps-scroll-y', () => {
				let categories = document.querySelectorAll('.menu__list_item')
				categories.forEach((category) => {
					let categoryTop = Math.floor(category.getBoundingClientRect().y)
					let title = category.querySelector('.list-item__title')
					if (title) {
						if (categoryTop <= 72) {
							title.classList.add('sticky')
						}
						else if (categoryTop + category.offsetHeight <= Math.floor(title.getBoundingClientRect().y) + 72) {
							title.classList.add('sticky-close')
							title.classList.remove('sticky')
						}
						else if (categoryTop > 72) {
							title.classList.remove('sticky')
							title.classList.remove('sticky-close')
						}
					}
				})
			})
		},
		setPreview(image, name, e) {
			let target = e.target
			while (!target.classList.contains('menu__sublist_item')) {
				target = target.parentNode
			}
			if (!target.classList.contains('menu__sublist_item-hover')) {
				target.classList.add('menu__sublist_item-hover')
				let preview = this.$parent.$refs.preview
				let targetTop = target.getBoundingClientRect().y
				let targetCenter = Math.floor(targetTop + (target.offsetHeight / 2))
				if (!preview.classList.contains('preview-show')) {
					preview.classList.add('preview-show')
				}
				preview.style.top = `${targetCenter}px`
				let previewImage = preview.firstChild
				previewImage.setAttribute('src', image)
				previewImage.setAttribute('alt', name)
			}
		},
		removePreview(e) {
			let target = e.target
			while (!target.classList.contains('menu__sublist_item')) {
				target = target.parentNode
			}
			target.classList.remove('menu__sublist_item-hover')
			let preview = this.$parent.$refs.preview
			let previewImage = preview.firstChild
			setTimeout(() => {
				if (!document.querySelector('.menu__sublist_item-hover')) {
					previewImage.setAttribute('src', '')
					previewImage.setAttribute('alt', '')
					preview.classList.remove('preview-show')
				}
			}, 100)
		}
	}
}
</script>

<style lang="scss"></style>
