import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const Store = new Vuex.Store({
	actions: {
		async fetchContent(state) {
			console.log('Get data..')
			const res = await fetch(`${window.pageConfig.url.pathToData.response}`)
			const content = await res.json()
			state.commit('SET_DATA', content.content)
			state.commit('SET_STATUS', content.status)
			if (content.status === 'success') {
				console.log('Data loaded')
			}
		}
	},
	state: {
		data: null,
		status: null
	},
	getters: {
		allContent(state) {
			return state.data
		},
		sections(state) {
			return state.data.sections
		},
		panoramas(state) {
			return state.data.panoramas
		},
		videos(state) {
			return state.data.videos
		},
		allCategories(state) {
			let catArr = []
			let i = 0
			catArr[0] = {
				id: 0,
				name: 'Все',
				active: true
			}
			for (i; i < state.data.sections.length; i++) {
				let category = state.data.sections[i]
				category.active = false
				catArr.push(category)
			}
			catArr[i + 1] = {
				id: state.data.sections.length + 1,
				name: 'Фото',
				active: false
			}
			return catArr
		},
		videoCategories(state) {
			// return state.content.filter((e) => e.title && e.body)
			let vCatArr = []
			for (let i = 0; i < state.data.sections.length; i++) {
				let newCategory = state.data.videos.filter((e) => e.section_id === (i + 1))
				vCatArr.push(newCategory)
			}
			return vCatArr
		}
	},
	mutations: {
		SET_DATA: (state, payload) => {
			state.data = payload
		},
		SET_STATUS: (state, payload) => {
			state.status = payload
		}
	}
})

export default Store
