<template>
	<div style="visibility: hidden; position: absolute">
		{{ config }}
		<br />
		{{ configURL }}
		<br />
		{{ configAssets }}
	</div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */

export default {
	name: 'AppConfig',
	components: {
	},
	data() {
		return {
			config: window.pageConfig
		}
	},
	computed: {
		configURL() { return this.config.url },
		configAssets() { return this.config.url.pathToAssets }
	}
}
</script>

<style lang="scss"></style>
