<template>
	<div class="preloader">
		<div class="preloader__pattern"></div>
		<div class="preloader__bg-image"></div>
		<div class="preloader__counter">
			<svg id="svg" viewPort="0 0 148 148" xmlns="http://www.w3.org/2000/svg">
				<circle r="74" cx="74" cy="74" stroke-dasharray="565.48" stroke-dashoffset="0"></circle>
				<circle id="bar" r="71" cx="74" cy="74" stroke-dasharray="446.1" :stroke-dashoffset="counterBar"></circle>
			</svg>
			<div class="preloader__counter_inner">{{ currentIndex }}</div>
		</div>
		<div class="preloader__maps" v-show="false">
			<img v-for="map in mapName" :src="mapUrl(map)" alt="Map render main" :key="map">
		</div>
	</div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */

// const WARNING_MAP = {}

export default {
	name: 'Preloader',
	props: ['counterDuration', 'publicPath'],
	data() {
		return {
			timing: null,
			duration: null,
			delay: null,
			currentIndex: 0,
			mapName: ['main', 'second', 'third']
		}
	},
	computed: {
		counterBar() {
			let bar = 0
			let c = Math.PI * (71 * 2)

			if (bar < 0) {
				bar = 0
			}
			if (bar > 100) {
				bar = 100
			}

			bar = ((100 - this.currentIndex) / 100) * c
			return bar
		}
	},
	mounted() {
		let maps = document.querySelectorAll('.preloader__maps img')
		let mapMain = maps[0]
		if (mapMain) {
			console.log('maps found, main:', mapMain.getAttribute('src'))
		}
		// let mapSecond = maps[1]
		let mapThird = maps[2]
		mapThird.onload = () => {
			this.delay = setTimeout(this.runTiming, 2000)
		}
	},

	methods: {
		// Start timer
		runTiming() {
			let interval = this.counterDuration / 100
			this.timing = setInterval(this.changeCurrentIndex, interval)
		},

		changeCurrentIndex() {
			if (this.currentIndex < 100) {
				this.currentIndex = +this.currentIndex + 1
			}
			else {
				clearInterval(this.timing)
				this.duration = setTimeout(this.closePreloader, 1000)
			}
		},
		closePreloader() {
			this.$emit('update:preloaderActive', false)
		},
		mapUrl(name) {
			let map = ''
			if (this.publicPath !== '') {
				let windowWidth = window.innerWidth
				let aspectRatio = window.devicePixelRatio
				if (windowWidth <= 768) {
					map = `${this.publicPath}img/map-${name}-mobile.png`
					if (aspectRatio > 1) {
						map = `${this.publicPath}img/map-${name}-mobile@2x.png`
					}
				}
				else if (windowWidth > 768 && windowWidth < 1540) {
					map = `${this.publicPath}img/map-${name}.png`
					if (aspectRatio > 1) {
						map = `${this.publicPath}img/map-${name}@2x.png`
					}
				}
				else {
					map = `${this.publicPath}img/map-${name}@2x.png`
				}
			}
			return map
		}
	},

	beforeDestroy() {
		// Delete timer
		clearInterval(this.timing)
	}
}
</script>

