<template>
	<div id="app">
<!--		<aside>-->
<!--			{{ $t('tmpStuff') }}-->
<!--		</aside>-->
		<appConfig ref="config" />
		<Header :preloader-active="preloaderActive" :mobile-menu-open.sync="mobileMenuOpen" :is-mobile="isMobile" />
		<transition-group name="component-fade" mode="out-in">
			<Preloader v-if="preloaderActive"
				key="preloader"
				:public-path="publicPath"
				:counter-duration="1000"
				:preloader-active.sync="preloaderActive" />
			<Main key="main"
				v-if="dataLoaded"
				:public-path="publicPath"
				:preloader-active="preloaderActive"
				:window-width="windowWidth"
				:window-height="windowHeight"
				:mobile-menu-open="mobileMenuOpen"
				:is-mobile="isMobile"
				:is-tablet="isTablet" />
		</transition-group>

	</div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import AppConfig from './components/AppConfig'
import Header from './components/Header'
import Preloader from './components/Preloader'
import Main from './components/Main'

export default {
	name: 'App',
	components: {
		AppConfig,
		Header,
		Preloader,
		Main
	},
	data() {
		return {
			...mapState(['status']),
			publicPath: '',
			preloaderActive: true,
			dataLoaded: false,
			mobileMenuOpen: false,
			windowWidth: window.innerWidth,
			windowHeight: window.innerHeight
		}
	},
	computed: {
		isTablet() {
			let t = false
			if (this.windowWidth < 1024 && this.windowWidth >= 768) {
				t = true
			}
			this.$emit('update:isTablet', t)
			return t
		},
		isMobile() {
			let m = false
			if (this.windowWidth < 768) {
				m = true
			}
			this.$emit('update:isMobile', m)
			return m
		}
	},
	methods: mapActions(['fetchContent']),
	mounted() {
		this.fetchContent()
		window.onload = () => {
			if (this.$store.state.status !== null) {
				this.dataLoaded = true
			}
			this.publicPath = this.$refs.config.configAssets
			// this.publicPath = process.env.BASE_URL
		}
		window.addEventListener('resize', () => {
			this.windowWidth = window.innerWidth
			this.windowHeight = window.innerHeight
			console.log(this.windowHeight, this.windowWidth)
		})

		// Panellum documentation https://pannellum.org/documentation/overview/
	}
}
</script>

<style lang="scss">
	.component-fade-enter-active, .component-fade-leave-active {
		transition: opacity .3s ease;
	}
	.component-fade-enter, .component-fade-leave-to
		/* .component-fade-leave-active до версии 2.1.8 */ {
		opacity: 0;
	}
	span[mode] {
		display: block;
		margin: 0;
		height: 100%;
	}
</style>
