<template>
	<div class="video"
		:class="{'video-active': isVideoPlayerActive || isPanoramaPlayerActive}"
		v-show="isVideoPlayerActive || isPanoramaPlayerActive"
		@click="closeHint">
		<div class="video__info">
			<button class="btn btn_close" type="button" @click="destroyPlayer">
				<Icon class="icon" name="closer" />
			</button>
			<h2 class="video__title" v-show="!isMobile"></h2>
			<button class="btn btn_fullscreen" type="button" v-show="!isMobile">
				<Icon class="icon" name="fullscreen-white" />
				<Icon class="icon" name="fullscreen-off-white" />
			</button>
		</div>
		<div class="video__hint" v-if="playerHintVisible">
			<div class="hint__image">
				<Icon class="icon icon_left" name="arrow-2-white_right" />
				<Icon class="icon circle" name="circle" />
				<Icon class="icon shape" name="shape" />
				<Icon class="icon icon_right" name="arrow-2-white_right" />
			</div>
			<p class="hint__text" v-if="isMobile">Коснитесь пальцем<br /> и перетягивайте для обзора панорамы</p>
			<p class="hint__text" v-else>Зажмите левую кнопку мыши<br /> и потяните для обзора панорамы</p>
		</div>
		<div class="video__hint" v-show="portraitHintVisible">
			<div class="hint__image">
				<Icon class="icon phone_opacity" name="phone-1" />
				<Icon class="icon arrow" name="arrow" />
				<Icon class="icon phone" name="phone-2" />
			</div>
			<p class="hint__text">Для комфортного просмотра<br />переверните устройство<br />в горизонтальное положение</p>
		</div>
		<div class="video__next" v-show="playerNextPreloader">
			<p class="next__text">Следующее видео</p>
			<h3 class="next__title">{{nextTitle}}</h3>
			<button class="btn btn_next" type="button">
				<svg id="svg" viewPort="0 0 88 88" xmlns="http://www.w3.org/2000/svg">
					<circle r="44" cx="44" cy="44" stroke-dasharray="565.48" stroke-dashoffset="0"></circle>
					<circle id="bar" r="41" cx="44" cy="44" stroke-dasharray="446.1" :stroke-dashoffset="counterBar"></circle>
				</svg>
				<Icon class="icon" name="next-white" />
			</button>
			<button class="btn btn_cancel" type="button">Отменить</button>
		</div>
		<div class="video__controls" v-show="playerControlsVisible">
			<button class="btn btn_prev" type="button">
				<Icon class="icon" name="next-white" />
			</button>
			<button class="btn btn_play" type="button">
				<Icon class="icon replay" name="replay-white" />
				<Icon class="icon play" name="play" />
				<span class="pause">Пауза</span>
			</button>
			<button class="btn btn_next" type="button">
				<Icon class="icon" name="next-white" />
			</button>
			<div class="time"></div>
			<div class="timeline">
				<div class="timeline__bar"></div>
			</div>
			<div class="time time_end"></div>
			<button class="btn btn_sound" type="button">
				<Icon class="icon on" name="sound-on" />
				<Icon class="icon off" name="sound-off" />
				<Icon class="icon half" name="sound-half" />
				<span class="sound__box" ref="soundBox">
					<span class="sound__bar" ref="soundBar"></span>
				</span>
			</button>
		</div>
		<div class="video__wrapper" id="panoramaWrapper" v-show="isPanoramaPlayerActive"></div>
		<div id="videoWrapper" class="video__wrapper" v-show="!isPanoramaPlayerActive">
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
// import Kaleidoscope from 'kaleidoscopejs'
// import pannellum from 'pannellum'
import Clappr from 'clappr'
import Video360 from 'clappr-video360'

export default {
	name: 'Video',
	props: ['isMobile', 'windowWidth', 'windowHeight'],
	data() {
		return {
			// publicPath: process.env.BASE_URL,
			playerHintVisible: true,
			portraitHintVisible: false,
			isVideoPlayerActive: false,
			isPanoramaPlayerActive: false,
			playerControlsVisible: false,
			playerCurrentVideoID: null,
			currentIndex: 0,
			nextTitle: '',
			playerNextPreloader: false
		}
	},
	computed: {
		...mapGetters(['videoCategories']),
		counterBar() {
			let bar = 0
			let c = Math.PI * (71 * 2)

			if (bar < 0) {
				bar = 0
			}
			if (bar > 100) {
				bar = 100
			}

			bar = ((100 - this.currentIndex) / 100) * c
			return bar
		}
	},
	mounted() {
		let pannellum = document.createElement('script')
		pannellum.async = true
		pannellum.src = `${this.$parent.publicPath}js/pannellum.js`
		document.head.appendChild(pannellum)
	},
	methods: {
		initVideo(data) {
			let container = document.querySelector('.video')
			let closeButton = container.querySelector('.btn_close')
			let fullscreenButton = container.querySelector('.btn_fullscreen')
			let title = container.querySelector('.video__title')
			title.innerText = data.name
			// init
			if (data.extension === 'jpg') {
				this.isPanoramaPlayerActive = true
				let panoramaContainer = container.querySelector('#panoramaWrapper')
				// eslint-disable-next-line no-undef
				this.player = pannellum.viewer('panoramaWrapper', {
					type: 'equirectangular',
					panorama: data.url,
					// panorama: `${this.$parent.publicPath}img/panorama.jpg`,
					autoLoad: true,
					showControls: false,
					height: window.innerHeight,
					width: window.innerWidth
				})
				//
				let infoBox = container.querySelector('.video__info')
				let info = infoBox.cloneNode(true)
				setTimeout(() => {
					panoramaContainer.prepend(info)
					infoBox.style.display = 'none'
					closeButton = info.querySelector('.btn_close')
					fullscreenButton = info.querySelector('.btn_fullscreen')
					title = info.querySelector('.video__title')
					title.innerText = data.name

					window.onresize = () => {
						this.player.resize({
							height: window.innerHeight,
							width: window.innerWidth
						})
					}

					closeButton.onclick = () => {
						this.player.destroy()
						infoBox.style.display = ''
						this.isPanoramaPlayerActive = false
						fullscreenButton.classList.remove('btn_fullscreen-close')
					}

					fullscreenButton.onclick = () => {
						this.player.toggleFullscreen()
						fullscreenButton.classList.toggle('btn_fullscreen-close')
					}
				}, 500)
			}
			else {
				this.isVideoPlayerActive = true
				let categoryAllVideo = this.videoCategories[data.section_id - 1]
				let currentIndex = categoryAllVideo.findIndex((e) => e.id === data.id)
				if (currentIndex < 0) { currentIndex = 0 }
				this.playerCurrentVideoID = currentIndex
				this.playerControlsVisible = true
				//
				let player = new Clappr.Player({
					source: data.url,
					plugins: {
						container: [Video360],
						loadExternalPluginsFirst: true
					},
					width: window.innerWidth,
					height: window.innerHeight,
					autoplay: true
				})

				player.attachTo(container.querySelector('#videoWrapper'))
				player.play()
				player.unmute()
				//
				let playerBox = document.querySelector('div[data-player]')
				if (playerBox) {
					let controlsBox = container.querySelector('.video__controls')
					let infoBox = container.querySelector('.video__info')
					let nextBox = container.querySelector('.video__next')
					//
					let info = infoBox.cloneNode(true)
					let next = nextBox.cloneNode(true)
					let controls = controlsBox.cloneNode(true)
					infoBox.style.display = 'none'
					nextBox.style.display = 'none'
					controlsBox.style.display = 'none'
					playerBox.prepend(info)
					playerBox.append(next)
					playerBox.append(controls)
					controls.style.display = ''
					let currentTime = controls.querySelector('.time')
					let leftTime = controls.querySelector('.time_end')
					let timeline = controls.querySelector('.timeline')
					let bar = controls.querySelector('.timeline__bar')
					let playButton = controls.querySelector('.btn_play')
					closeButton = playerBox.querySelector('.btn_close')
					fullscreenButton = playerBox.querySelector('.btn_fullscreen')
					title = playerBox.querySelector('.video__title')
					title.innerText = data.name
					//
					title = info.querySelector('.video__title')
					fullscreenButton = info.querySelector('.btn_fullscreen')
					closeButton = info.querySelector('.btn_close')
					player.configure({
						events: {
							onPlay: () => {
								player.getPlugin('click_to_pause').disable()
								let resizeDelay = null
								let plugin360 = player.getPlugin('Video360')
								window.onresize = () => {
									if (resizeDelay) {
										clearTimeout(resizeDelay)
										resizeDelay = null
									}

									resizeDelay = setTimeout(() => {
										let width = window.innerWidth
										let height = window.innerHeight

										player.resize({ height, width })
										plugin360.viewer.setSize({ width, height })
										plugin360.viewer.render()
									}, 100)
								}
							},
							onTimeUpdate: () => {
								let currentTimeAll = Math.floor(player.getCurrentTime())
								// let currentTimeSeconds = currentTimeAll % 60
								// let currentTimeMinutes = (currentTimeAll - currentTimeSeconds) / 60
								let timeAll = Math.floor(player.getDuration())
								let leftTimeAll = (timeAll - currentTimeAll)
								let leftTimeSeconds = leftTimeAll % 60
								let leftTimeMinutes = (leftTimeAll - leftTimeSeconds) / 60
								// if (currentTimeSeconds < 10) {
								// 	currentTimeSeconds = `0${currentTimeSeconds}`
								// }
								if (leftTimeSeconds < 10) {
									leftTimeSeconds = `0${leftTimeSeconds}`
								}
								// currentTime.innerText = `${currentTimeMinutes}:${currentTimeSeconds}`
								currentTime.innerText = player.core.mediaControl.displayedPosition
								leftTime.innerText = `-${leftTimeMinutes}:${leftTimeSeconds}`
								// bar.style.width = `${(currentTimeAll / timeAll) * 100}%`
								bar.style.width = `${player.core.mediaControl.currentSeekBarPercentage}%`
							},
							onEnded: () => {
								// let currentTimeAll = player.getDuration()
								// let currentTimeSeconds = currentTimeAll % 60
								// let currentTimeMinutes = (currentTimeAll - currentTimeSeconds) / 60
								let buttonNextBig = document.querySelector('.video__next .btn_next')
								let buttonNext = document.querySelector('.video__controls .btn_next')
								let buttonPrev = document.querySelector('.video__controls .btn_prev')
								// currentTime.innerText = `${currentTimeMinutes}:${currentTimeSeconds}`
								currentTime.innerText = player.core.mediaControl.displayedDuration
								leftTime.innerText = '00:00'
								bar.style.width = '100%'
								playButton.classList.add('btn_play-replay')
								if (this.playerCurrentVideoID < categoryAllVideo.length - 1) {
									this.playerNextPreloader = true
									this.nextTitle = categoryAllVideo[this.playerCurrentVideoID + 1].name
									document.querySelector('.player-poster[data-poster]').style.backgroundImage = categoryAllVideo[this.playerCurrentVideoID + 1].preview_big
									let interval = 5000 / 100
									let timing = setInterval(() => {
										if (this.currentIndex < 100) {
											this.currentIndex = +this.currentIndex + 1
										}
										else {
											clearInterval(timing)
											if (this.playerCurrentVideoID === 0) {
												buttonPrev.classList.remove('btn_prev-disable')
											}
											this.playerCurrentVideoID += 1
											if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
												buttonNext.classList.add('btn_prev-disable')
											}
											let nextVideo = categoryAllVideo[this.playerCurrentVideoID]
											player.load(nextVideo.url)
											title.innerText = nextVideo.name
											// player.load(`${this.$parent.publicPath}img/MISis_SCNnext_draft.mp4`)
											player.play()
											playButton.classList.remove('btn_play-replay')
											this.playerNextPreloader = false
											this.currentIndex = 0
											document.querySelector('.player-poster[data-poster]').style.backgroundImage = ''
										}
									}, interval)
									let buttonCancel = document.querySelector('.video__next .btn_cancel')
									buttonCancel.onclick = () => {
										clearInterval(timing)
										this.playerNextPreloader = false
										this.currentIndex = 0
										document.querySelector('.player-poster[data-poster]').style.backgroundImage = categoryAllVideo[this.playerCurrentVideoID].preview_big
									}
									playButton.onclick = () => {
										clearInterval(timing)
										this.playerNextPreloader = false
										this.currentIndex = 0
										player.play()
										playButton.classList.remove('btn_play-replay')
										document.querySelector('.player-poster[data-poster]').style.backgroundImage = ''
									}
									buttonNextBig.onclick = () => {
										clearInterval(timing)
										if (this.playerCurrentVideoID === 0) {
											buttonPrev.classList.remove('btn_prev-disable')
										}
										this.playerCurrentVideoID += 1
										if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
											buttonNext.classList.add('btn_prev-disable')
										}
										let nextVideo = categoryAllVideo[this.playerCurrentVideoID]
										// player.load(`${this.$parent.publicPath}img/MISis_SCNnext_draft.mp4`)
										player.load(nextVideo.url)
										title.innerText = nextVideo.name
										this.playerNextPreloader = false
										this.currentIndex = 0
										player.play()
										playButton.classList.remove('btn_play-replay')
										document.querySelector('.player-poster[data-poster]').style.backgroundImage = ''
									}
								}
							}
						}
					})
					// Resize
					//
					// Show and hide controls
					let timer = setTimeout(() => {
						controls.classList.remove('video__controls-hide')
					}, 2000)
					window.onmousemove = (e) => {
						clearTimeout(timer)
						let target = e.target
						let isControls = false
						while (target.classList.contains('video')) {
							if (target.classList.contains('video__controls')) {
								isControls = true
							}
							target = target.parentNode
						}
						if (controls.classList.contains('video__controls-hide') || isControls || e.target.classList.contains('video__controls') || e.target.closest('video__controls')) {
							clearTimeout(timer)
							controls.classList.remove('video__controls-hide')
						}
						else {
							timer = setTimeout(() => {
								controls.classList.add('video__controls-hide')
							}, 2000)
						}
					}
					//
					// Next and Previous videos
					let nextVideoButton = controls.querySelector('.btn_next')
					let prevVideoButton = controls.querySelector('.btn_prev')
					if (this.playerCurrentVideoID === 0) {
						prevVideoButton.classList.add('btn_prev-disable')
					}
					prevVideoButton.onclick = () => {
						if (this.playerCurrentVideoID > 0) {
							if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
								nextVideoButton.classList.remove('btn_next-disable')
							}
							this.playerCurrentVideoID -= 1
							let prevVideo = categoryAllVideo[this.playerCurrentVideoID]
							// player.load(`${this.$parent.publicPath}img/MISis_SCN_draft.mp4`)
							player.load(prevVideo.url)
							title.innerText = prevVideo.name
							player.play()
							playButton.classList.remove('btn_play-replay')
						}
					}
					//
					if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
						nextVideoButton.classList.add('btn_next-disable')
					}
					nextVideoButton.onclick = () => {
						if (this.playerCurrentVideoID < categoryAllVideo.length - 1) {
							if (this.playerCurrentVideoID === 0) {
								prevVideoButton.classList.remove('btn_prev-disable')
							}
							this.playerCurrentVideoID += 1
							let nextVideo = categoryAllVideo[this.playerCurrentVideoID]
							// player.load(`${this.$parent.publicPath}img/MISis_SCNnext_draft.mp4`)
							player.load(nextVideo.url)
							title.innerText = nextVideo.name
							player.play()
						}
					}
					//
					// Play and pause video
					playButton.onclick = () => {
						if (playButton.classList.contains('btn_play-paused')) {
							playButton.classList.toggle('btn_play-paused')
							player.play()
						}
						else if (playButton.classList.contains('btn_play-replay')) {
							playButton.classList.toggle('btn_play-replay')
							player.play()
						}
						else {
							playButton.classList.toggle('btn_play-paused')
							player.pause()
						}
					}
					//
					// Mute and unmute sound
					let soundButton = controls.querySelector('.btn_sound')
					let soundIcons = soundButton.querySelectorAll('.icon')
					let soundBar = soundButton.querySelector('.sound__box')
					let soundBarInner = soundBar.querySelector('.sound__bar')
					soundButton.onmouseover = () => {
						soundButton.classList.add('btn_sound-tuning')
					}
					soundButton.onmouseout = () => {
						soundButton.classList.remove('btn_sound-tuning')
					}
					soundIcons.forEach((soundIcon) => {
						// eslint-disable-next-line no-param-reassign
						soundIcon.onclick = () => {
							if (soundButton.classList.contains('btn_sound-muted')) {
								soundButton.classList.toggle('btn_sound-muted')
								player.unmute()
								soundBarInner.style.height = `100%`
							}
							else {
								soundButton.classList.toggle('btn_sound-muted')
								soundButton.classList.remove('btn_sound-half')
								player.mute()
								soundBarInner.style.height = `0%`
							}
						}
					})
					// soundBar.onmousedown = player.core.mediaControl.startVolumeDrag()
					soundBar.onmousedown = () => {
						soundBar.classList.add('active')
					}
					soundBar.onmouseup = () => {
						soundBar.classList.remove('active')
					}
					soundBar.onmouseleave = () => {
						if (soundBar.classList.contains('active')) {
							soundBar.classList.remove('active')
						}
					}
					// soundBar.onmousemove = player.core.mediaControl.mousemoveOnVolumeBar()
					soundBar.onmousemove = (e) => {
						if (soundBar.classList.contains('active')) {
							let pos = e.pageY
							let barPos = soundBar.getBoundingClientRect().y
							let value = Math.floor((soundBar.offsetHeight - (pos - barPos)) / soundBar.offsetHeight * 100)
							if (value > 100) {
								value = 100
							}
							soundBarInner.style.height = `${value}%`
							player.core.mediaControl.core.setVolume(value)
							if (soundButton.classList.contains('btn_sound-muted') && value > 5) {
								soundButton.classList.toggle('btn_sound-muted')
								soundButton.classList.add('btn_sound-half')
								if (value >= 50) {
									soundButton.classList.remove('btn_sound-half')
								}
							}
							else if (value >= 50) {
								soundButton.classList.remove('btn_sound-half')
							}
							else if (value <= 50 && value > 5) {
								soundButton.classList.add('btn_sound-half')
							}
							else if (value <= 5) {
								soundButton.classList.remove('btn_sound-half')
								soundButton.classList.add('btn_sound-muted')
							}
						}
					}
					soundBar.onclick = (e) => {
						let soundBarY = soundBar.getBoundingClientRect().y
						let clickY = e.pageY
						let soundBarHeight = soundBar.offsetHeight
						let diffY = clickY - soundBarY
						let value = Math.floor(((soundBarHeight - diffY) / soundBarHeight) * 100)
						if (value > 100) {
							value = 100
						}
						soundBarInner.style.height = `${value}%`
						player.core.mediaControl.core.setVolume(value)
						if (soundButton.classList.contains('btn_sound-muted') && value > 5) {
							soundButton.classList.toggle('btn_sound-muted')
							soundButton.classList.add('btn_sound-half')
							if (value >= 50) {
								soundButton.classList.remove('btn_sound-half')
							}
						}
						else if (value >= 50) {
							soundButton.classList.remove('btn_sound-half')
						}
						else if (value <= 50 && value > 5) {
							soundButton.classList.add('btn_sound-half')
						}
						else if (value <= 0) {
							soundButton.classList.remove('btn_sound-half')
							soundButton.classList.add('btn_sound-muted')
						}
					}
					//
					// Set new time
					timeline.onclick = (e) => {
						let x = e.pageX
						let timeX = timeline.getBoundingClientRect().x
						let timeWidth = timeline.offsetWidth
						let percent = ((x - timeX) / timeWidth) * 100
						player.seekPercentage(percent)
						bar.style.width = `${percent}%`
					}
					//
					// Close player
					closeButton.onclick = () => {
						this.playerControlsVisible = false
						this.playerCurrentVideoID = null
						title.innerText = ''
						player.destroy()
						playButton.classList.remove('btn_play-paused', 'btn_play-replay')
						this.isVideoPlayerActive = false
						infoBox.style.display = ''
						fullscreenButton.classList.remove('btn_fullscreen-close')
						// source: `${this.publicPath}img/${data.filename}.${data.extension}`
					}
					//
					// Fullscreen
					fullscreenButton.onclick = () => {
						player.core.mediaControl.toggleFullscreen()
						fullscreenButton.classList.toggle('btn_fullscreen-close')
					}
				}
			}
		},
		destroyPlayer() {
			this.isVideoPlayerActive = false
			this.isPanoramaPlayerActive = false
		},
		closeHint() {
			console.log(this.windowHeight, this.windowWidth)
			if (this.portraitHintVisible) {
				this.portraitHintVisible = false
			}
			if (this.playerHintVisible) {
				this.playerHintVisible = false
				if (this.windowHeight > this.windowWidth) {
					this.portraitHintVisible = true
				}
			}
		}
	}
}
</script>

<style lang="scss">
	.video__hint .hint__image .icon_right,
	.video__controls .btn_prev .icon {
		transform: rotate(180deg);
	}
</style>
