<template>
	<nav class="map__categories">
		<ul class="categories__list container" :class="{'side-scroll': $parent.isMobile && $parent.windowHeight > $parent.windowWidth}">
			<li class="categories__item"
				:class="{'categories__item-active': categoriesActiveList[category.id]}"
				v-for="(category, index) in categories"
				:key="index">
				<button class="btn categories__btn" @click="$parent.setActive(index)">
					<span class="categories__icon categories__icon_all" v-if="category.id === 0">
						<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 1]}"></span>
						<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 2]}"></span>
						<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 3]}"></span>
						<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 4]}"></span>
					</span>
					<span class="categories__icon categories__icon_circle" v-if="category.id > 0" :style="{backgroundColor: categoriesColors[category.id]}">
						<Icon class="icon" :name="`section-${category.id}`" v-if="category.id < categories.length - 1" />
						<Icon class="icon" name="photo" v-else-if="+category.id === categories.length - 1"  />
					</span>
					<span class="title">{{category.name}}</span>
				</button>
			</li>
		</ul>
	</nav>
</template>

<script>
export default {
	name: 'MapCategories',
	props: ['categories', 'categoriesActiveList', 'categoriesColors'],
	computed: {}
}
</script>

<style lang="scss"></style>
