<template>
	<div :id="mapId"
		class="map__item"
		:class="{active: isActive}"
		ref="map"
		:style="{'transform': translateX}"
		@click="closeContent"
		@touchstart="startMapMove"
		@touchend="endMapMove"
		@touchleave="endMapMove"
		@touchmove="moveMapMove">
		<div class="map__render">
			<img alt="Map render main" ref="mapImage">
			<!--				:srcset="mapUrl(mapId, mapSize[0]) + ' 768w, ' + mapUrl(mapId, mapSize[1]) + ' 1540w'"-->
			<!--				sizes="(min-width: 768px) 768px, (min-width: 1540px) 1540px, 100vw"-->
		</div>
		<ul class="map__markers">
			<li class="marker" v-for="(marker, index) in mPoints"
				:key="'marker' + index"
				:class="{'marker-hover': marker.isHovering,'marker-active': marker.isActive,'marker-visited': marker.isVisited, 'marker-closing': markerClose}"
				:style="{top: marker.top + '%', left: marker.left + '%'}"
				@click.stop="closeContent"
				@mouseover="startHover(marker.id)"
				@mouseout="endHover(marker.id)">
				<div class="marker__wrapper"
					v-show="+activeCategory === 0 || marker.categoryID === activeCategory"
					:key="'markerWrapper' + marker.id">
					<button class="btn btn_marker"
							type="button"
							:style="{background: categoriesColors[marker.categoryID]}"
							@click="openContent(marker.id)">
						<Icon class="icon" :name="`section-${marker.categoryID}`" v-if="marker.categoryID < 5" />
						<Icon class="icon" name="photo" v-else />
						<span class="count" v-if="marker.content.length > 1">{{marker.content.length}}</span>
					</button>
					<span class="title">{{ allCategories[marker.categoryID].name }}</span>
					<div class="marker__content" v-if="marker.content.length > 1" >
						<ul class="content__list">
							<li class="content__item"
								v-for="(item, index) in marker.content"
								:key="marker.id + 'item' + index"
								@click="$parent.initVideo(item)">
								<div class="content__preview content__preview_mobile" v-if="isMobile">
									<img :src="marker.content[0].preview_mobile" :alt="marker.content[0].name" />
								</div>
								<h3 class="content__title">{{ item.name }}</h3>
								<div class="content__marker" :style="{background: categoriesColors[marker.categoryID]}">{{ allCategories[marker.categoryID].name }}</div>
								<button class="content__play" type="button">
									<Icon class="icon" name="play" v-if="marker.categoryID < 5" />
									<Icon class="icon" name="eye" v-else />
								</button>
							</li>
						</ul>
					</div>
					<div class="marker__content marker__content_single" v-else @click="$parent.initVideo(marker.content[0])">
						<div class="content__preview content__preview_mobile" v-if="isMobile">
							<img :src="marker.content[0].preview_mobile" :alt="marker.content[0].name" />
						</div>
						<div class="content__preview content__preview_single" v-else>
							<img :src="marker.content[0].preview_big" :alt="marker.content[0].name" />
							<button class="content__play content__play_single" type="button">
								<Icon class="icon" name="play" v-if="marker.categoryID < 5" />
								<Icon class="icon" name="eye" v-else />
							</button>
						</div>
						<h3 class="content__title content__title_single">{{ marker.content[0].name }}</h3>
						<p class="content__category content__category_single">{{ allCategories[marker.categoryID].name }}</p>
						<button class="content__play content__play_single" type="button" v-if="isMobile">
							<Icon class="icon" name="play" v-if="marker.categoryID < 5" />
							<Icon class="icon" name="eye" v-else />
						</button>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */
import { mapGetters } from 'vuex'
import Icon from '../modules/ml-components/icon'

export default {
	name: 'MapItem',
	components: {
		Icon
	},
	props: ['mapId', 'map', 'categories', 'activeCategory', 'hoverPoint', 'categoriesColors', 'mapContent', 'mapPointsContent', 'mapPoints', 'windowWidth', 'isMobile'],
	data() {
		return {
			mPointsContent: this.mapPointsContent,
			mPoints: this.mapPoints,
			markerClose: false,
			mapName: ['main', 'second', 'third'],
			mapSize: ['mobile', 'normal', ''],
			mapLink: '',
			isActive: false,
			startX: 0,
			scrollLeft: 0,
			// startPosition: 'center',
			translateX: 0
		}
	},
	computed: {
		...mapGetters(['allContent', 'sections', 'panoramas', 'videos', 'videoCategories', 'allCategories'])
	},
	mounted() {
		// eslint-disable-next-line no-unused-vars
		setTimeout(this.FindOverflowMarkers, 1000)
		window.onresize = this.FindOverflowMarkers
		this.setMapUrl()
	},
	watch: {
		hoverPoint() {
			if (this.hoverPoint !== null) {
				this.startHover(this.hoverPoint)
			}
			else {
				this.endHover(this.hoverPoint)
				this.mPoints.forEach((point) => {
					// eslint-disable-next-line no-param-reassign
					point.isHovering = false
				})
			}
		},
		windowWidth() {
			this.setMapUrl()
		},
		activeCategory() {
			this.FindOverflowMarkers()
		},
		mapId() {
			this.FindOverflowMarkers()
		}
	},
	methods: {
		FindOverflowMarkers() {
			let markers = document.querySelectorAll('.map__markers .marker')
			markers.forEach((marker) => {
				let content = marker.querySelector('.marker__content')
				let children = content.childNodes
				let contentHeight = 0
				children.forEach((child) => {
					contentHeight += child.offsetHeight
					if (child.classList) {
						if (!child.classList.contains('.marker__content_bottom')) {
							contentHeight += 13 + 6
						}
					}
				})
				let markerPosition = marker.getBoundingClientRect()
				if (contentHeight > Math.floor(markerPosition.y)) {
					content.classList.add('marker__content_bottom')
				}
				else {
					content.classList.remove('marker__content_bottom')
				}
			})
		},
		mapUrl(size) {
			let map = ''
			if (this.$parent.publicPath) {
				let aspectRatio = window.devicePixelRatio
				switch (this.mapSize[size]) {
				case 'mobile':
					map = `${this.$parent.publicPath}img/map-${this.mapId}-mobile.png`
					if (aspectRatio > 1) {
						map = `${this.$parent.publicPath}img/map-${this.mapId}-mobile@2x.png`
					}
					break
				case 'normal':
					map = `${this.$parent.publicPath}img/map-${this.mapId}.png`
					if (aspectRatio > 1) {
						map = `${this.$parent.publicPath}img/map-${this.mapId}@2x.png`
					}
					break
				default:
					map = `${this.$parent.publicPath}img/map-${this.mapId}@2x.png`
					break
				}
			}
			this.mapLink = map
			this.$refs.mapImage.setAttribute('src', this.mapLink)
		},
		setMapUrl() {
			let index = 0
			if (this.windowWidth > 768 && this.windowWidth <= 1540) {
				index = 1
			}
			else if (this.windowWidth > 1540) {
				index = 2
			}
			this.mapLink = this.mapUrl(index)
		},
		openContent(id) {
			this.markerClose = true
			setTimeout(() => {
				let activePointID = this.mPoints.filter((e) => e.isActive === true)
				if (activePointID.length > 0) {
					this.mPoints[activePointID[0].id].isActive = false
					this.mPoints[activePointID[0].id].isVisited = true
					this.mPoints[id].isHovering = false
				}
				this.mPoints[id].isActive = true
				this.mPoints[id].isVisited = true
				this.mPoints[id].isHovering = true
				if (activePointID.length > 0 && activePointID[0].id === id) {
					this.mPoints[id].isActive = false
					this.mPoints[id].isVisited = true
					this.mPoints[id].isHovering = false
				}
				this.markerClose = false
			}, 100)
		},
		closeContent() {
			let activePointID = this.mPoints.filter((e) => e.isActive === true)
			if (activePointID.length > 0) {
				this.markerClose = true
				setTimeout(() => {
					this.mPoints[activePointID[0].id].isActive = false
					this.mPoints[activePointID[0].id].isVisited = true
					this.mPoints[activePointID[0].id].isHovering = false

					this.markerClose = false
				}, 100)
			}
		},
		startHover(marker) {
			for (let i = 0; i < this.mPoints.length; i++) {
				this.mPoints[i].isHovering = false
			}
			this.mPoints[+marker].isHovering = true
		},
		endHover(marker) {
			this.mPoints[+marker].isHovering = false
		},
		startMapMove(e) {
			this.isActive = true
			this.startX = e.changedTouches[0].pageX
		},
		endMapMove() {
			this.isActive = false
		},
		moveMapMove(e) {
			let map = this.$refs.map.getBoundingClientRect()
			let mapWidth = map.width
			let rightLimit = (mapWidth - this.$parent.windowWidth) / 2
			let leftLimit = ((mapWidth - this.$parent.windowWidth) / 2) * -1
			if (!this.isActive) return
			if (e) {
				e.preventDefault()
				let x = 0
				x = e.changedTouches[0].pageX
				let walk = 0
				// if (x > -50 || x < 50) {
				walk = x - this.startX // scroll-fast
				// }
				// console.log(walk, x, this.startX, rightLimit, leftLimit)
				if (walk > 0) {
					if (walk > rightLimit) {
						this.scrollLeft = leftLimit
					}
					else {
						this.scrollLeft = 0
					}
				}
				else if (walk < 0) {
					if (walk < leftLimit) {
						this.scrollLeft = rightLimit
					}
					else {
						this.scrollLeft = 0
					}
				}
				else {
					this.scrollLeft = 0
				}
				// console.log(this.scrollLeft)
				this.translateX = `translateX(${this.scrollLeft * -1}px)`
			}
		}
	}
}
</script>

