<template>
	<header class="header">
		<div class="header__toggler" v-if="isMobile">
			<button class="btn btn_toggler" type="button" :class="{'btn_toggler-active': !mobileMenuOpen}" @click="toggleMobileMenu">На карте</button>
			<button class="btn btn_toggler" type="button" :class="{'btn_toggler-active': mobileMenuOpen}" @click="toggleMobileMenu">Списком</button>
		</div>
		<div class="header__logo">
			<div v-if="preloaderActive" class="logo logo_white">
				<Icon class="svg-icon" :name="logo" />
			</div>
			<div v-else class="logo">
				<Icon class="svg-icon" :name="logo" />
			</div>
		</div>
	</header>
</template>

<script>
export default {
	name: 'Header',
	props: ['preloaderActive', 'mobileMenuOpen', 'isMobile'],
	data() {
		return {}
	},
	computed: {
		logo() {
			let logoName = ''
			if (this.preloaderActive) {
				logoName = 'logo-white'
			}
			else {
				logoName = 'logo'
			}
			return logoName
		}
	},
	methods: {
		toggleMobileMenu() {
			let m = !this.mobileMenuOpen
			this.$emit('update:mobileMenuOpen', m)
		}
	},
	mounted() {}
}
</script>

<style lang="scss"></style>
