<template>
	<div class="categories">
		<ul class="categories__list side-scroll">
			<li class="categories__item"
				v-for="(category, index) in categories"
				:key="index">
				<button class="btn"
						:class="{'btn-active': categoriesActiveList[category.id]}"
						@click="$parent.setActive(index)">
					{{category.name}}
					<span class="after" v-if="category.id === 0"></span>
					<span class="after" :style="{backgroundColor: categoriesColors[category.id]}" v-else></span>
				</button>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'Categories',
	components: {},
	props: ['categories', 'categoriesActiveList', 'categoriesColors'],
	computed: {}
}
</script>

<style lang="scss"></style>
